/**
 *
 * PageSettingsModal
 *
 */

import React from 'react';
// import styled from 'styled-components';

import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Input,
  Label,
  Col,
  Button,
} from 'reactstrap';

import CbToggle from '../CbToggle';
import createFakeEvent from '../../cb-utils/createFakeEvent';
import { CBEvent } from 'utils/types';

// import { FormattedMessage } from 'react-intl';
// import messages from './messages';

export interface PageSettingsModalRtn extends IState {
  id: string;
  parentId: string;
}

interface IProps {
  title: string;
  pageInfo: {
    id: string;
    parentId: string;
    name: string;
    isParam: boolean;
    defaultValue: string;
    isChild: boolean;
  };
  onDismiss: () => void;
  onClose: (rtn: PageSettingsModalRtn) => void;
}

interface IState {
  name: string;
  isParam: boolean;
  defaultValue: string;
}

class PageSettingsModal extends React.Component<IProps, IState> {
  state: IState = {
    name: this.props.pageInfo.name,
    isParam: this.props.pageInfo.isParam,
    defaultValue: this.props.pageInfo.defaultValue,
  };

  render() {
    return (
      <Modal isOpen={true} toggle={this.props.onDismiss} autoFocus={false}>
        <Form onSubmit={this.onSubmit}>
          <ModalHeader>{this.props.title}</ModalHeader>

          <ModalBody>
            <FormGroup row>
              <Col sm={3}>
                <Label>Name</Label>
              </Col>
              <Col sm={9}>
                <Input
                  data-cy="page-name"
                  type="text"
                  placeholder="My Page Name"
                  value={this.state.name}
                  autoFocus={this.state.name === ''}
                  onChange={this.onSettingChange.bind(this, 'name')}
                />
              </Col>
            </FormGroup>

            {this.props.pageInfo.isChild && (
              <div>
                <FormGroup row>
                  <Col sm={3}>
                    <Label>Parameter</Label>
                  </Col>
                  <Col sm={9}>
                    <CbToggle
                      onChange={(val: boolean) =>
                        this.onSettingChange('isParam', createFakeEvent(val))
                      }
                      value={this.state.isParam}
                    />
                  </Col>
                </FormGroup>

                {this.state.isParam && (
                  <FormGroup row>
                    <Col sm={3}>
                      <Label>Default Value</Label>
                    </Col>
                    <Col sm={9}>
                      <Input
                        type="text"
                        placeholder="foo"
                        value={this.state.defaultValue}
                        onChange={this.onSettingChange.bind(
                          this,
                          'defaultValue',
                        )}
                      />
                    </Col>
                  </FormGroup>
                )}
              </div>
            )}
          </ModalBody>

          <ModalFooter>
            <Button color="default" onClick={this.props.onDismiss}>
              Cancel
            </Button>
            <Button
              type="submit"
              color="primary"
              onClick={this.onSubmit}
              disabled={
                !this.state.name ||
                (this.state.isParam && !this.state.defaultValue)
              }
            >
              Apply
            </Button>
          </ModalFooter>
        </Form>
      </Modal>
    );
  }

  onSettingChange(key: keyof IState, e: CBEvent) {
    this.setState({
      ...this.state,
      [key]: e.target.value,
    });
  }

  onSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    this.props.onClose({
      ...this.state,
      id: this.props.pageInfo.id,
      parentId: this.props.pageInfo.parentId,
    });
  };
}

export default PageSettingsModal;



